"use client";

export type AdmissionStatus = "pending" | "approved" | "rejected";

export type AdmissionApplication = {
  id: string;
  fullName: string;
  age: number;
  gender: string;
  primaryCourse: string;
  secondaryCourse: string;
  email: string;
  status: AdmissionStatus;
  submittedAt: string;
};

export type GeneratedCredential = {
  admissionId: string;
  email: string;
  fullName: string;
  studentNumber: string;
  tempPassword: string;
  mustChangePassword: boolean;
  createdAt: string;
};

const ADMISSION_KEY = "tclass_admissions";
const CREDENTIALS_KEY = "tclass_generated_credentials";

function readJson<T>(key: string, fallback: T): T {
  if (typeof window === "undefined") return fallback;
  try {
    const raw = localStorage.getItem(key);
    return raw ? (JSON.parse(raw) as T) : fallback;
  } catch {
    return fallback;
  }
}

function writeJson<T>(key: string, value: T): void {
  if (typeof window === "undefined") return;
  localStorage.setItem(key, JSON.stringify(value));
}

export function getAdmissions(): AdmissionApplication[] {
  return readJson<AdmissionApplication[]>(ADMISSION_KEY, []);
}

export function saveAdmissions(rows: AdmissionApplication[]): void {
  writeJson(ADMISSION_KEY, rows);
}

export function submitAdmission(
  payload: Omit<AdmissionApplication, "id" | "status" | "submittedAt">
): AdmissionApplication {
  const rows = getAdmissions();
  const row: AdmissionApplication = {
    ...payload,
    id: crypto.randomUUID(),
    status: "pending",
    submittedAt: new Date().toISOString(),
  };
  rows.unshift(row);
  saveAdmissions(rows);
  return row;
}

export function getGeneratedCredentials(): GeneratedCredential[] {
  return readJson<GeneratedCredential[]>(CREDENTIALS_KEY, []);
}

export function saveGeneratedCredentials(rows: GeneratedCredential[]): void {
  writeJson(CREDENTIALS_KEY, rows);
}

function generateStudentNumber(): string {
  const year = new Date().getFullYear().toString().slice(-2);
  const random = Math.floor(1000 + Math.random() * 9000);
  return `${year}-1-1-${random}`;
}

function generateTempPassword(): string {
  const chars = "ABCDEFGHJKLMNPQRSTUVWXYZabcdefghjkmnpqrstuvwxyz23456789!@#$%";
  let out = "";
  for (let i = 0; i < 10; i++) {
    out += chars[Math.floor(Math.random() * chars.length)];
  }
  return out;
}

export function approveAdmission(id: string): GeneratedCredential | null {
  const rows = getAdmissions();
  const idx = rows.findIndex((r) => r.id === id);
  if (idx < 0) return null;

  rows[idx] = { ...rows[idx], status: "approved" };
  saveAdmissions(rows);

  const creds = getGeneratedCredentials();
  const existing = creds.find((c) => c.admissionId === id);
  if (existing) return existing;

  const created: GeneratedCredential = {
    admissionId: rows[idx].id,
    email: rows[idx].email,
    fullName: rows[idx].fullName,
    studentNumber: generateStudentNumber(),
    tempPassword: generateTempPassword(),
    mustChangePassword: true,
    createdAt: new Date().toISOString(),
  };
  creds.unshift(created);
  saveGeneratedCredentials(creds);
  return created;
}

export function rejectAdmission(id: string): void {
  const rows = getAdmissions();
  const idx = rows.findIndex((r) => r.id === id);
  if (idx < 0) return;
  rows[idx] = { ...rows[idx], status: "rejected" };
  saveAdmissions(rows);
}

export function markPasswordChanged(email: string): void {
  const creds = getGeneratedCredentials();
  const next = creds.map((c) =>
    c.email.toLowerCase() === email.toLowerCase()
      ? { ...c, mustChangePassword: false }
      : c
  );
  saveGeneratedCredentials(next);
}
